button,
html,
body,
input,
select,
textarea {
  font-family: NotoSans, Arial, sans-serif; }

header .navbar.navbar-new-bottom {
  padding: 0; }
  header .navbar.navbar-new-bottom .navbar-nav > li:hover, header .navbar.navbar-new-bottom .navbar-nav > li.active > a {
    background-color: #E3E3E3;
    color: #9B1BBC; }
  header .navbar.navbar-new-bottom .navbar-nav > li a {
    color: #9B1BBC;
    font-weight: 600;
    font-size: 16px;
    text-transform: uppercase;
    display: block;
    padding: 1.5em 1em;
    text-align: center; }
  header .navbar.navbar-new-bottom .navbar-nav > li + li a {
    position: relative; }
    header .navbar.navbar-new-bottom .navbar-nav > li + li a::before {
      content: "";
      position: absolute;
      left: -1px;
      top: 35%;
      bottom: 35%;
      width: 2px;
      opacity: .05;
      background-color: #292929; }
